@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.banner {
  position: relative;
  padding-top: 164px;
  padding-bottom: 160px;

  @include mixin.laptop {
    padding-top: 134px;
    padding-bottom: 130px;
  }

  @include mixin.wide-tablet {
    padding-top: 104px;
    padding-bottom: 100px;
  }

  @include mixin.small-tablet {
    padding-top: 74px;
    padding-bottom: 70px;
  }

  @include mixin.phone {
    padding-top: 21px;
    padding-bottom: 64px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    @include mixin.small-tablet {
      align-items: flex-start;
    }
  }

  &__news {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
    margin-bottom: 52px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
      max-width: 250px;
    }

    .badge {
      padding: 0.2rem 1rem;
      font-family: var.$font-family2;
      font-size: 1.4rem;
      font-weight: 500;
      line-height: 1.428;
    }

    span {
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.3;
      color: var.$secondary;
      padding-bottom: 0;
      border-bottom: 1px solid var.$secondary;
      transition: 0.3s linear;

      @include mixin.phone {
        text-decoration: underline;
        border-bottom: unset;
      }
    }

    &:hover {
      span {
        color: var.$primary;
      }
    }

    svg {
      @include mixin.small-tablet {
        display: none;
      }
    }
  }

  &__title {
    max-width: 1198px;
    text-align: center;
    margin-bottom: 24px;

    @include mixin.small-tablet {
      text-align: left;
    }
  }

  &__subtitle {
    max-width: 600px;
    text-align: center;
    margin-bottom: 48px;

    @include mixin.small-tablet {
      text-align: left;
    }

    @include mixin.tablet {
      margin-bottom: 32px;
    }
  }

  &__btn {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    width: 100%;
    max-width: 414px;
    box-sizing: border-box;
    padding: 12px 16px;
  }

  &__before,
  &__after {
    content: '';
    position: absolute;
    z-index: -1;
    pointer-events: none;
    border: 3px solid var.$primary;

    @include mixin.tablet {
      border-width: 1.5px;
    }
  }

  &__before {
    top: -150px;
    right: 4%;
    border-top: none;
    border-left: none;
    border-bottom-right-radius: 100px;
    width: 190%;
    height: 110%;
    animation: lineAnim1 0.8s linear 0.3s;

    @include mixin.tablet {
      width: 190%;
      height: 120%;
      border-bottom-right-radius: 50px;
    }
  }

  &__after {
    right: 0;
    bottom: 0;
    border-top-left-radius: 100px;
    border-bottom: none;
    border-right: none;
    width: 30.5%;
    height: 36%;
    animation: lineAnim2 0.5s linear 0.2s;

    @include mixin.tablet {
      width: 53%;
      height: 28%;
      border-top-left-radius: 50px;
    }
  }

  @keyframes lineAnim1 {
    0% {
      transform: translate(-100%, -100%);
    }

    100% {
      transform: translate(0, 0);
    }
  }

  @keyframes lineAnim2 {
    0% {
      transform: translate(100%, 100%);
    }

    100% {
      transform: translate(0, 0);
    }
  }
}

.visually-impaired {
  .banner {

    &:after,
    &:before {
      display: none !important;
    }
  }
}

.info-list {
  background: var.$tertiary;
  perspective: 600px;
  transform-style: preserve-3d;

  @include mixin.phone {
    background: transparent;
  }

  &__container {
    @include mixin.phone {
      padding-bottom: 16px;
    }
  }

  &__item {
    padding: 48px 35px;
    transition: 0.3s linear;
    flex-grow: 1;
    min-height: 100%;

    &:hover {
      background: var.$secondary;
    }

    @include mixin.phone {
      min-height: 148px;
      padding: 24px 17px;
      background: var.$tertiary;
      border-top-left-radius: 24px;
      border-bottom-right-radius: 24px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      transform: scale(0.8) translateY(-12%);
    }
  }

  .swiper-slide-active &__item {
    @include mixin.phone {
      transform: scale(1) translateY(0%);
    }
  }

  .swiper-slide {
    &:nth-child(1) {
      .info-list__item {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        text-align: left;
        padding-right: 12%;
        box-sizing: border-box;

        @include mixin.phone {
          align-items: flex-start;
        }
      }
    }

    &:nth-child(2) {
      .info-list__item {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mixin.phone {
          align-items: flex-start;
        }
      }
    }
  }

  &__title {
    margin-bottom: 24px;
    transition: 0.3s linear;
  }

  &__item:hover &__title {
    color: var.$light;
  }

  &__more {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1;
    color: var.$secondary;
    transition: 0.3s linear;

    &:hover,
    &:visited {
      color: var.$secondary;
    }

    svg {
      width: 16px;
      height: 16px;
    }

    path {
      transition: 0.3s linear;
    }
  }

  &__item:hover &__more {
    color: var.$light;

    path {
      stroke: var.$light;
    }
  }

  &__swiper {
    @include mixin.phone {
      padding-bottom: 12px;
    }
  }

  &__pagination {
    display: none;
    width: 72px;
    bottom: 0 !important;
    justify-content: center;
    align-items: center;
    gap: 4px;

    @include mixin.phone {
      display: flex;
    }

    span {
      display: block;
      width: 16px;
      height: 4px;
      border-radius: 100px;
      background: #E9ECF2;
      margin: 0;
      transition: 0.3s linear;

      &.swiper-pagination-bullet-active {
        width: 32px;
        background: var.$primary;
      }
    }
  }
}

.analytic {
  padding-top: 128px;
  padding-bottom: 128px;

  @include mixin.small-laptop {
    padding-top: 112px;
    padding-bottom: 112px;
  }

  @include mixin.tablet {
    padding-top: 96px;
    padding-bottom: 96px;
  }

  @include mixin.small-tablet {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  @include mixin.phone {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .title-2 {
    margin-bottom: 16px;
  }

  .subtitle-2 {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }

    @include mixin.phone {
      margin-bottom: 16px;
    }
  }

  &__header {
    padding-bottom: 24px;
    border-bottom: 1px solid var.$line2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    margin-bottom: 48px;

    @include mixin.small-laptop {
      flex-direction: column-reverse;
      align-items: flex-start;
    }

    @include mixin.small-tablet {
      padding-bottom: 0;
      border-bottom: unset;
      margin-bottom: 32px;
    }
  }

  &__types-wrap {
    max-width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;

    @include mixin.small-tablet {
      padding-bottom: 24px;
      border-bottom: 1px solid var.$line2;
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__types {
    flex-grow: 1;
    max-width: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 49px;
  }

  &__type {
    font-weight: 700;
    color: #B1B5BD;
    cursor: pointer;

    @include mixin.small-tablet {
      white-space: nowrap;
    }

    &--active {
      color: var.$secondary;
    }
  }

  &__more {
    padding: 12px 48px;
    box-sizing: border-box;
    font-size: 1.6rem;
    font-weight: 400;

    @include mixin.small-tablet {
      width: 100%;
    }
  }

  &__subtypes {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    gap: 12px;
    margin-bottom: 48px;
    overflow-y: auto;
    scrollbar-width: none;

    @include mixin.small-tablet {
      flex-wrap: wrap;
      overflow-y: unset;
      margin-bottom: 32px;
    }

    &::-webkit-scrollbar {
      display: none;
    }

    button {
      padding: 12px 24px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      transition: 0.5s linear;
      white-space: nowrap;
    }
  }
}

.report {
  box-sizing: border-box;
  width: 100%;
  padding: 24px 49.64px 24px 32px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;

  &__charts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    height: 100%;

    @include mixin.small-tablet {
      gap: 36px;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 1.8rem;
    width: 100%;

    .badge--empty {
      display: flex;
      align-items: center;
      gap: 7.33px;
      padding: 8px 16px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      color: var.$gray2;
    }
  }

  &__title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 15px;
    text-align: left;
    white-space: normal;
    margin-bottom: 8px;
  }

  &__info {
    position: relative;
    display: inline-block;
    flex-shrink: 0;
    align-self: flex-start;
  }

  &__info-btn {
    padding: 0;
    margin: 0;
    border: none;
    padding: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: transparent;
    background-image: url('@images/icons/icon_info.svg');
    background-size: contain;
    background-position: center;
    cursor: pointer;
  }

  &__info-body {
    position: absolute;
    top: 120%;
    right: 120%;
    min-width: 180px;
    max-width: 300px;
    padding: 8px 10px;
    box-sizing: border-box;
    border: 1px solid #c0c0c0;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.08);
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.8);
    z-index: 10;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.2;
    color: #000;
    opacity: 0;
    pointer-events: none;
    transition: 0.3s linear;
  }

  &__info:hover &__info-body {
    opacity: 1;
  }

  &__filters {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    min-width: 100%;
  }

  &__filter {
    min-width: 120px;

    button {
      min-width: 100%;
    }
  }

  &__filter-results {
    text-align: center;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.5;
    color: var.$secondary;
    margin-bottom: 2px;
    display: flex;
    justify-content: center;
    gap: 35px;
  }

  &__body {
    width: 100%;
    margin-top: 12px;

    .highcharts-scrolling {
      flex-wrap: nowrap;
      overflow-y: hidden;
      scrollbar-width: none;
    
      &::-webkit-scrollbar {
        display: none;
      }
    }
  }
}

.report-total {
  display: none;
  width: 100%;
  box-sizing: border-box;
  padding: 32px 32px 32px 32px;

  @include mixin.tablet {
    margin-bottom: 32px;
  }

  &:hover {
    background: var.$secondary;
  }

  &--active {
    display: block;
  }

  &__header {
    font-size: 2.4rem;
    font-weight: 500;
    line-height: 1.33;
    color: var.$light;
    margin-bottom: 24px;
  }

  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 0px 32px;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item-long {
    min-width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  &__title {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    color: rgba(var.$light, 0.5);
    margin-bottom: 2px;
  }

  &__title-main {
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.66;
    color: var.$light;
    margin-right: 15px;
  }

  &__text {
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1.55;
    color: var.$light;
  }

  &__text-big {
    min-width: 100%;
    font-size: 32px;
    font-weight: 700;
    line-height: 1.25;
  }

  &__percent {
    display: flex;
    gap: 0px 10px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.428;
  }

  &__text-large {
    font-size: 40px;
    font-weight: 700;
    line-height: 1.1;
  }
}

.news {
  padding-top: 48px;
  padding-bottom: 128px;

  @include mixin.small-laptop {
    padding-top: 32px;
    padding-bottom: 112px;
  }

  @include mixin.tablet {
    padding-top: 32px;
    padding-bottom: 96px;
  }

  @include mixin.small-tablet {
    padding-top: 16px;
    padding-bottom: 80px;
  }

  @include mixin.phone {
    padding-top: 0;
    padding-bottom: 64px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 48px;

    @include mixin.tablet {
      margin-bottom: 32px;
    }

    .badge {
      padding: 12px 16px;
      flex-grow: 1;
      max-width: 414px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;

      @include mixin.tablet {
        max-width: unset;
        width: 100%;
        box-sizing: border-box;
      }
    }
  }

  &__head {
    max-width: 630px;
    flex-grow: 1;

    .title-2 {
      margin-bottom: 16px;
    }

    .subtitle-2 {
      @include mixin.tablet {
        margin-bottom: 16px;
      }
    }
  }

  .row {
    margin-left: -10px;
    margin-right: -10px;
    gap: 20px 0;

    & > div {
      padding: 0 10px;
    }
  }
}

.news-item {
  height: 100%;
  box-sizing: border-box;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  transition: 0.3s linear;

  &:hover {
    background: var.$secondary;
  }

  &__tags,
  &__files,
  &__head {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    gap: 8px;
  }

  &__head {
    width: 100%;
    margin-bottom: 24px;
  }

  &__files {
    flex-grow: 1;
  }

  &__tag,
  &__file {
    padding: 2px 12px;
    font-family: var.$font-family2;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.428;
  }

  &__type {
    display: flex;
    align-items: stretch;
  }

  &:hover &__file {
    background: #EDF1F4;
    color: var.$secondary;
  }

  &__date {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.66;
    color: var.$gray;
    margin-bottom: 16px;
  }

  &__title {
    flex-grow: 1;
    margin-bottom: 62px;
    line-height: 1.66;
    max-width: 95%;
    transition: 0.3s linear;

    @include mixin.wide-tablet {
      margin-bottom: 47px;
    }

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &:hover &__title {
    color: var.$light;
  }

  &__more {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 6px;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1;
    color: var.$secondary;
    transition: 0.3s linear;

    svg {
      width: 15px;
    }

    path {
      transition: 0.3s linear;
    }
  }

  &:hover &__more {
    color: var.$light;

    path {
      stroke: var.$light;
    }
  }
}

.activity {
  position: relative;
  padding-top: 96px;
  padding-bottom: 119px;
  overflow: hidden;

  @include mixin.small-laptop {
    padding-top: 72px;
    padding-bottom: 112px;
  }

  @include mixin.tablet {
    padding-top: 48px;
    padding-bottom: 96px;
  }

  @include mixin.small-tablet {
    padding-top: 24px;
    padding-bottom: 80px;
  }

  @include mixin.phone {
    padding-top: 0;
    padding-bottom: 64px;
  }

  &__before,
  &__after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 64%;
    height: 514px;
    box-sizing: border-box;
    border-top-left-radius: 507px;
    z-index: -1;
    border-top: 416px solid var.$tertiary;
    border-left: 416px solid var.$tertiary;
    transition: 0.3s linear;

    @include mixin.small-tablet {
      display: none;
    }
  }

  &__after {
    right: unset;
    left: 36%;
    border-top: 450px solid #fff;
    border-left: 450px solid #fff;
    z-index: 0;
    transform-origin: 100% 100%;
  }

  &.animate &__after {
    animation: 0.8s animLine linear forwards;
  }

  &.no-animate &__after {
    animation: 0.8s noAnimLine linear forwards;
  }

  @keyframes animLine {
    0% {
      width: 64%;
    }

    50% {
      width: 23%;
      transform: rotateZ(0deg);
    }

    100% {
      width: 23%;
      transform: rotateZ(-90deg);
    }
  }

  @keyframes noAnimLine {
    0% {
      width: 64%;
    }

    50% {
      width: 23%;
      transform: rotateZ(0deg);
    }

    100% {
      width: 23%;
      transform: rotateZ(-90deg);
    }
  }

  &[data-current="0"]:before {
    border-top: 416px solid #FDF6EB;
    border-left: 416px solid #FDF6EB;
  }

  &[data-current="1"]:before {
    border-top: 416px solid #EDF1F4;
    border-left: 416px solid #EDF1F4;
  }

  &[data-current="2"]:before {
    border-top: 416px solid #E2F2EA;
    border-left: 416px solid #E2F2EA;
  }

  &[data-current="3"]:before {
    border-top: 416px solid #EFE9F4;
    border-left: 416px solid #EFE9F4;
  }

  .container {
    position: relative;
    z-index: 2;
  }

  .title-2 {
    margin-bottom: 16px;
  }

  .subtitle-2 {
    margin-bottom: 16px;
  }

  &__more {
    padding: 12px 32px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var.$light;
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
      width: 100%;
      box-sizing: border-box;
    }

    &:hover,
    &:active {
      color: var.$light;
    }
  }

  .activity-slider {
    width: 100%;
    overflow: hidden;

    &__body {
      margin-bottom: 60px;
      width: 100%;

      @include mixin.small-laptop {
        margin-bottom: 48px;
      }

      @include mixin.tablet {
        margin-bottom: 36px;
      }

      @include mixin.small-tablet {
        margin-bottom: 24px;
      }

      @include mixin.phone {
        margin-bottom: 12px;
      }
    }

    &__pagination {
      display: none;
      height: 4px;
      bottom: 0 !important;
      justify-content: center;
      align-items: center;
      gap: 4px;

      @include mixin.phone {
        display: flex;
      }

      span {
        display: block;
        width: 16px;
        height: 4px;
        border-radius: 100px;
        background: #E9ECF2;
        margin: 0;
        transition: 0.3s linear;

        &.swiper-pagination-bullet-active {
          width: 32px;
          background: var.$primary;
        }
      }
    }

    &__item {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include mixin.small-tablet {
        flex-direction: column-reverse;
        align-items: flex-start;
        gap: 24px;
        background: var.$tertiary;
        padding: 24px 17px;
        border-top-left-radius: 24px;
        border-bottom-right-radius: 24px;
      }
    }

    &__data {
      max-width: 460px;
    }

    &__title {
      margin-bottom: 16px;
    }

    &__text {
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      color: var.$gray3;
    }

    &__img {
      position: relative;
      width: 40.7%;
      max-height: 196px;

      img {
        width: 100%;
        max-height: 100%;
        object-fit: contain;
      }

      @include mixin.phone {
        max-height: 72px;
      }

      .enbek-light {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 1;
        transition: 0.1s linear;
        filter: grayscale(1);
      }

      &:hover .enbek-light {
        opacity: 1;
        filter: grayscale(0);
      }

      .enbek-dark {
        position: relative;
        opacity: 1;
        z-index: 1;
        transition: 0.1s linear;
      }

      &:hover .enbek-dark {
        opacity: 0;
      }
    }

    .swiper-slide-active {
      .activity-slider {
        &__data {
          opacity: 0;
          animation: 0.3s slideTop 0.3s linear forwards;

          @keyframes slideTop {
            0% {
              transform: translateY(150px);
              opacity: 0;
            }

            100% {
              transform: translateY(0);
              opacity: 1;
            }
          }
        }

        &__img {
          opacity: 0;
          animation: 0.3s opacityAnim 0.9s linear forwards;

          @keyframes opacityAnim {
            0% {
              opacity: 0;
            }

            100% {
              opacity: 1;
            }
          }
        }
      }
    }

    &__control {
      display: flex;
      align-items: center;
      gap: 24px;

      @include mixin.phone {
        display: none;
      }
    }

    &__btn {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: rgba(var.$secondary, 0.1);
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        width: 24.66px;
        height: 21.45px;
      }

      &--left {
        svg {
          transform-origin: center;
          transform: rotateZ(180deg);
        }
      }
    }

    &__counter {
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.6;
      color: var.$gray3;
    }
  }
}